  line-height: 40px;
  margin-left: -4px;
  padding-right: 18px;
  cursor: pointer;

}
.login li:first-child {
  margin-left: 0;
}
.login li:last-child:after {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  top: 0;
  bottom: 0;
  right: -3px;
  background-color: #374d63;
}

.login li:not(:first-child)  {
  border-left: 1px solid #374d63;
}
