.top-bar_icon li {
  height: 40px;
  padding-left: 16px;
  padding-right: 16px;
  border-right: 1px solid #17202a;
  border-left: 1px solid #374d63;
  line-height: 40px;

}
.top-bar_icon li {
  margin-right: -4px;
  &:last-child:after, &:first-child:after {
    content: "";
    display: block;
    position: absolute;
    width: 1px;
    top: 0;
    bottom: 0;
  }
  &:last-child:after {
    right: -2px;
    background-color: #374d63;
  }
  &:first-child:after {
    left: -2px;
    background-color: #17202a;
