.menu-mobile {
  position: absolute;
  top: 30px;
  right: 0;
  background-color: #e8e8e8;
  height: 40px;
  width: 44px;
  cursor: pointer;
  z-index: 9999;
  display: none;
  border-radius: 3px;
  right: 10px;
}
.menu-mobile span {
  position: absolute;
  height: 3px;
  width: 25px;
  background-color: $base-color;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  @include transition-mixin($style: transform);
  &:before {
    content: '';
    position: absolute;
    top: -8px;
    background-color: $base-color;
    height: 3px;
    width: 25px;
    @include transition-mixin($style: transform);
  }
  &:after {
    content: '';
    position: absolute;
    background-color: $base-color;
    height: 3px;
    width: 25px;
    @include transition-mixin($style: transform);
    top: 8px;
  }
}
.menu-mobile.active {
  background-color: #e8e8e8;
}
.menu-mobile.active span {
  background-color: transparent;
}
.menu-mobile.active span:before {
  transform: rotate(45deg);
  top: 0;
}
.menu-mobile.active span:after {
  transform: rotate(-45deg);
  top: 0;
}

.menu.active {
  display: initial;

}

